
.tile-box-wrap {
    float:left;
    width:100px;
    height:100px;
    margin:5px;
}
.tile-box {
    width:100%;
    height:100%;
    background-color:gray; /* Safari */
    transition-duration: 0.3s;
}
.tile-box-wrap:hover .tile-box {
    width:90px;
    height:90px;
    margin:5px;
    cursor:pointer;
}



