
.startup-box-wrap {
  position: fixed;
  background-color: black;
  bottom: 50px;
  width: 700px;
  height: 500px;
}
.startup-box {
  background-color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.7;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}



